import "./ExtStatTracker.js";
import { getChatGPTAccessToken, getBrowser } from "../services/utils";
import {
  chatGptProvider,
  sendMessageFeedback,
  removeDialogs,
  genTitle,
} from "./providers/chatGpt.js";
import { openAIProvider } from "./providers/openAI.js";

class Background {
  constructor() {
    this.baseSettings = {
      triggerMode: "always",
      theme: "auto",
      siteHelper: true,
      language: [
        { value: "auto", name: "Auto detect", selected: true },
        { value: "english", name: "English", selected: false },
        { value: "chinese", name: "Chinese", selected: false },
        { value: "spanish", name: "Spanish", selected: false },
        { value: "french", name: "French", selected: false },
        { value: "korean", name: "Korean", selected: false },
        { value: "japanese", name: "Japanese", selected: false },
        { value: "german", name: "German", selected: false },
        { value: "portuguese", name: "Portuguese", selected: false },
      ],
      removeDi: false,
      provider: {
        apiKey: "",
        currentTab: "web",
        guideMode: "default",
        models: [
          {
            value: "text-davinci-003",
            name: "text-davinci-003",
            selected: true,
          },
          {
            value: "gpt-3.5-turbo",
            name: "gpt-3.5-turbo",
            selected: false,
          },
        ],
      },
      viewingOptions: {
        tab: false,
        window: false,
        openedTab: null,
        openedWindow: null,
      },
    };

    this.checkClasses();

    this.setStorage();
    this.initMessageListener();
    this.closeViewListener();
    this.initPortListener();
  }

  checkClasses() {
    chrome.storage.local.get(["appl1_sgpt_lastDownClasses"], (result) => {
      if (result && result.appl1_sgpt_lastDownClasses) {
        const t = result.appl1_sgpt_lastDownClasses;
        const d = Date.now();
        const diff = d - t;

        if (diff >= 86400000) {
          this.downLoadClasses();
        }
      } else {
        this.downLoadClasses();
      }
    });
  }

  downLoadClasses() {
    let host;

    if (getBrowser() === "EDGE") {
      host = "getedgegpt.com";
    } else if (getBrowser() === "CHROME") {
      host = "getsearchgpt.com";
    }

    fetch(`https://${host}/api/classes/`)
      .then((res) => res.json())
      .then((data) => {
        this.baseSettings.classes = data[0].classes;
        chrome.storage.local.set({
          appl1_sgpt_lastDownClasses: Date.now(),
        });
        chrome.storage.local.set({
          appl1_sgpt_settings: this.baseSettings,
        });
      });
  }

  setStorage() {
    chrome.storage.local.get(["appl1_sgpt_settings"], (result) => {
      if (result && result.appl1_sgpt_settings) {
        return;
      } else {
        chrome.storage.local.set({
          appl1_sgpt_settings: this.baseSettings,
        });
      }
    });
  }

  initMessageListener() {
    chrome.runtime.onMessage.addListener((message, sender, callback) => {
      switch (message.action) {
        case "GET_ACCESS_TOKEN":
          getChatGPTAccessToken((data) => callback(data));
          break;

        case "OPEN_VIEW":
          this.openView(message.type, callback);
          break;

        case "FOCUSE":
          this.focuse(message.type, message.id);
          break;

        case "GET_STYLES":
          if (sender.tab) {
            callback("tab");
          } else {
            callback("popup");
          }
          break;

        case "OPEN_LOGIN_PAGE":
          chrome.tabs.create(
            {
              url: `https://chat.openai.com/auth/login`,
            },
            (t) => {
              let id = t.id;

              chrome.webRequest.onSendHeaders.addListener(
                (details) => {
                  if (id !== null) {
                    if (
                      details.initiator === "https://chat.openai.com" &&
                      details.tabId === id
                    ) {
                      chrome.tabs.sendMessage(details.tabId, {
                        action: "SHOW_BANNER",
                        idTab: sender.tab.id,
                      });
                    }
                  }
                },
                {
                  urls: ["https://chat.openai.com/api/auth/session"],
                },
                ["requestHeaders"]
              );
            }
          );
          break;

        case "RELOAD_APP":
          chrome.tabs.sendMessage(message.tabId, {
            action: "RELOAD_APP",
          });
          break;

        case "FOCUS_TAB":
          chrome.tabs.update(message.tabId, { active: true });
          break;

        case "OPEN_SUPPORT_PAGE":
          const url =
            getBrowser() === "CHROME"
              ? `https://chrome.google.com/webstore/detail/${chrome.runtime.id}/support`
              : `https://microsoftedge.microsoft.com/addons/detail/${chrome.runtime.id}`;

          chrome.tabs.create({
            url,
          });
          break;

        case "OPEN_RATEUS_PAGE":
          const link =
            getBrowser() === "CHROME"
              ? `https://chrome.google.com/webstore/detail/${chrome.runtime.id}/reviews`
              : `https://microsoftedge.microsoft.com/addons/detail/${chrome.runtime.id}`;

          chrome.tabs.create({
            url: link,
          });
          break;

        case "SET_SETTINGS_TO_STORE":
          this.setSettingsToStore(message.data);
          break;

        case "GET_USER_THEME":
          this.getSettingsFromStore((data) => data).then((res) => {
            callback(res.theme);
          });
          break;

        case "FEEDBACK":
          getChatGPTAccessToken(({ accessToken }) => {
            sendMessageFeedback(accessToken, message.data);
          });
          break;

        case "REMOVE_DIALOGS":
          getChatGPTAccessToken(({ accessToken }) => {
            removeDialogs(accessToken);
          });
          break;

        case "GEN_TITLE":
          getChatGPTAccessToken(({ accessToken }) => {
            genTitle(accessToken, message.id, message.mId);
          });
          break;

        default:
          break;
      }

      return true;
    });
  }

  focuse(type, id) {
    if (type === "tab") {
      chrome.tabs.update(id, { active: true });
    } else if (type === "window") {
      chrome.windows.update(id, {
        focused: true,
      });
    }
  }

  openView(type, callback) {
    const _this = this;

    chrome.storage.local.get(["appl1_sgpt_settings"], (result) => {
      if (result.appl1_sgpt_settings) {
        const set = result.appl1_sgpt_settings;

        if (type === "tab") {
          if (set.viewingOptions.openedTab === null) {
            chrome.tabs.create(
              {
                url: chrome.runtime.getURL(`popup.html`),
              },
              (tab) => {
                set.viewingOptions.openedTab = tab.id;
                set.viewingOptions.tab = true;
                set.viewingOptions.window = false;

                _this.setSettingsToStore(set);
                callback();
              }
            );
          }
        } else if (type === "window") {
          if (set.viewingOptions.openedWindow === null) {
            chrome.windows.create(
              {
                focused: true,
                url: chrome.runtime.getURL(`popup.html`),
                height: 637, //639
                width: 414, //416
                type: "popup",
              },
              (w) => {
                set.viewingOptions.openedWindow = w.id;
                set.viewingOptions.tab = false;
                set.viewingOptions.window = true;

                _this.setSettingsToStore(set);
                callback();
              }
            );
          }
        }
      } else {
        return;
      }
    });
  }

  closeViewListener() {
    const _this = this;

    chrome.tabs.onRemoved.addListener((id) => {
      chrome.storage.local.get(["appl1_sgpt_settings"], (result) => {
        if (result.appl1_sgpt_settings) {
          const set = result.appl1_sgpt_settings;

          if (id === set.viewingOptions.openedTab) {
            set.viewingOptions.openedTab = null;
            set.viewingOptions.tab = false;
            set.viewingOptions.window = false;

            _this.setSettingsToStore(set);
          }
        }
      });
    });

    chrome.windows.onRemoved.addListener((id) => {
      chrome.storage.local.get(["appl1_sgpt_settings"], (result) => {
        if (result.appl1_sgpt_settings) {
          const set = result.appl1_sgpt_settings;

          if (id === set.viewingOptions.openedWindow) {
            set.viewingOptions.openedWindow = null;
            set.viewingOptions.tab = false;
            set.viewingOptions.window = false;

            _this.setSettingsToStore(set);
          }
        }
      });
    });
  }

  getSettingsFromStore() {
    return new Promise((resolve, reject) => {
      chrome.storage.local.get(["appl1_sgpt_settings"], (result) =>
        resolve(result.appl1_sgpt_settings)
      );
    });
  }

  setSettingsToStore(obj) {
    chrome.storage.local.set({
      appl1_sgpt_settings: obj,
    });
  }

  initPortListener() {
    chrome.runtime.onConnect.addListener((port) => {
      const controller = new AbortController();
      const signal = controller.signal;

      port.onDisconnect.addListener(() => {
        controller.abort();
      });

      port.onMessage.addListener((msg) => {
        this.generateAnswers(signal, msg.question, (event) => {
          port.postMessage(event);
        });
      });
    });
  }

  async generateAnswers(signal, question, onEvent) {
    const provider = await this.getSettingsFromStore((data) => data).then(
      (res) => res.provider
    );

    if (provider.currentTab === "web") {
      getChatGPTAccessToken(({ accessToken }) => {
        chatGptProvider(question, accessToken, signal, onEvent);
      });
    } else if (provider.currentTab === "gpt3") {
      openAIProvider(provider, question, signal, onEvent);
    }
  }
}

const b = new Background();
