import { getGuideMode, parsData } from "../../services/utils";

const openAIProvider = (provider, question, signal, onEvent) => {
  const model = provider.models.filter((el) => el.selected === true);
  const prompt = getGuideMode(provider.guideMode, question);
  const modelName = model[0].name;
  let body;
  let path;
  let result = "";

  if (modelName === "text-davinci-003") {
    body = {
      max_tokens: 800,
      model: model[0].value,
      prompt,
      stop: "\nA: ",
      stream: true,
      temperature: 0.3,
    };
    path = "completions";
  } else if (modelName === "gpt-3.5-turbo") {
    body = {
      max_tokens: 800,
      messages: [
        {
          content: prompt,
          role: "system",
        },
        {
          content: question,
          role: "user",
        },
      ],
      model: model[0].value,
      stream: true,
      temperature: 0.5,
    };
    path = "chat/completions";
  }

  const methodOptions = {
    method: "POST",
    signal: signal,
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${provider.apiKey}`,
    },
    body: JSON.stringify(body),
  };

  fetch(`https://api.openai.com/v1/${path}`, methodOptions).then((resp) => {
    if (resp.ok) {
      parsData(resp, (message) => {
        if (message === "[DONE]") {
          onEvent({ type: "done" });
          return;
        }

        let data;
        try {
          data = JSON.parse(message);
        } catch (err) {
          console.error(err);
          return;
        }

        let text;

        if (modelName === "text-davinci-003") {
          text = data.choices[0].text;
        } else if (modelName === "gpt-3.5-turbo") {
          text = data.choices[0].delta.content;
        }

        if (
          text === "<|im_end|>" ||
          text === "<|im_sep|>" ||
          text === undefined
        ) {
          return;
        }

        result += text;

        onEvent({
          type: "answer",
          data: {
            text: result,
            messageId: data.id,
          },
        });
      });
    }
  });
};

export { openAIProvider };
