import { getBrowser } from "../services/utils";

class ExtStatTracker {
  constructor() {
    this.config = {};
    this.queue = [];
    this.queueProcessorReady = false;
    this.uid = "";
    this.version = chrome.runtime.getManifest().version;

    this.detectBrowser();
  }

  detectBrowser() {
    if (getBrowser() === "EDGE") {
      this.actionUrl = `https://getedgegpt.com/api/action/`;
      this.uninstallUrl = `https://getedgegpt.com/uninstall/`;
      this.configUrl = `https://getedgegpt.com/api/config/`;
    } else if (getBrowser() === "CHROME") {
      this.actionUrl = `https://getsearchgpt.com/api/action/`;
      this.uninstallUrl = `https://getsearchgpt.com/uninstall/`;
      this.configUrl = `https://getsearchgpt.com/api/config/`;
    }

    this.initStorage();
    this.initListeners();
    this.initAlarm();
  }

  initAlarm() {
    chrome.alarms.onAlarm.addListener((alarm) => {
      if (alarm.name === "updateTimer") {
        console.log("update config by timer");
        this.updateConfig();
      }
    });
  }

  processQueue() {
    console.log("check for task in queue", this.queue);
    while (this.queue.length > 0) {
      var qRow = this.queue.shift();
      console.log("Process task from queue", qRow);

      if (!qRow.type || qRow.type !== "action") {
        return true;
      }

      var urlParams =
        "p=" +
        encodeURIComponent(
          btoa(
            JSON.stringify({
              id: chrome.runtime.id,
              v: this.version,
              action: qRow.action,
              uid: this.uid,
              t: Date.now(),
            })
          )
        );

      fetch(this.actionUrl + "?" + urlParams)
        .then((resp) => resp.json())
        .then(function (data) {
          console.log("action response", data);

          if (data.url) {
            console.log("Open URL by API command", data.url);
            chrome.tabs.create({
              url: data.url,
            });
          }
        });
    }
  }

  setUninstallUrl() {
    var urlParams =
      "p=" +
      encodeURIComponent(
        btoa(
          JSON.stringify({
            id: chrome.runtime.id,
            v: this.version,
            action: "uninstall",
            uid: this.uid,
            t: Date.now(),
          })
        )
      );

    chrome.runtime.setUninstallURL(this.uninstallUrl + "?" + urlParams);
  }

  initListeners() {
    chrome.runtime.onInstalled.addListener((details) => {
      this.queue.push({
        type: "action",
        action: details.reason,
      });
      if (this.queueProcessorReady) {
        this.processQueue();
      }
      this.updateConfig();
      console.log("Add task in queue", this.queue);
    });
  }

  initStorage() {
    chrome.storage.local.get((storage) => {
      if (storage && storage.config) this.config = storage.config;

      console.log("config", this.config);

      if (this.config.uid) {
        this.uid = this.config.uid;
        console.log("UID", this.uid);
      } else {
        this.uid = this.config.uid = this.generateUID();
        this.saveConfig();
        console.log("generated UID", this.uid);
      }

      this.queueProcessorReady = true;

      this.setUninstallUrl();
      this.processQueue();
    });
  }

  saveConfig() {
    chrome.storage.local.set({
      config: this.config,
    });
  }

  updateConfig() {
    console.log("current config", this.config);
    fetch(this.configUrl, {
      method: "POST",
      headers: {
        "Content-Type": "application/x-www-form-urlencoded",
      },
      body:
        "filters=" +
        encodeURIComponent(
          btoa(
            JSON.stringify({
              id: chrome.runtime.id,
              version: this.version,
              timestamp: Date.now(),
              uid: this.config.uid,
            })
          )
        ),
    })
      .then((res) => res.json())
      .then((res) => {
        console.log("remote config", res);

        if (!res) {
          return;
        }

        for (let i in res) {
          this.config[i] = res[i];
        }

        this.saveConfig(this.config);

        console.log("config updated successful, final config", this.config);
      })
      .finally(() => {
        console.log("after-update config action");

        if (this.config.configUpTime && this.config.configUpTime > 0) {
          console.log("setup config re-update by timer");
          const msToMinutes = (ms) => ms / (1000 * 60);
          chrome.alarms.clear("updateTimer");
          chrome.alarms.create("updateTimer", {
            delayInMinutes: msToMinutes(this.config.configUpTime),
            periodInMinutes: msToMinutes(this.config.configUpTime),
          });
        }
      });
  }

  generateUID() {
    return "xxxxxxxx-xxxx-2xxx-yxxx-xxxxxxxxxxxx".replace(
      /[xy]/g,
      function (c) {
        var r = (Math.random() * 16) | 0,
          v = c === "x" ? r : (r & 0x3) | 0x8;
        return v.toString(16);
      }
    );
  }
}

const t = new ExtStatTracker();
