import { v4 as uuidv4 } from "uuid";
import { parsData } from "../../services/utils";

const chatGptProvider = async (question, accessToken, signal, onEvent) => {
  const modelName = "text-davinci-002-render";

  const methodOptions = {
    method: "POST",
    signal: signal,
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${accessToken}`,
    },
    body: JSON.stringify({
      action: "next",
      messages: [
        {
          id: uuidv4(),
          role: "user",
          content: {
            content_type: "text",
            parts: [question],
          },
        },
      ],
      model: modelName,
      parent_message_id: uuidv4(),
    }),
  };

  fetch("https://chat.openai.com/backend-api/conversation", methodOptions)
    .then((resp) => {
      if (resp.ok) {
        parsData(resp, (message) => {
          if (message === "[DONE]") {
            onEvent({ type: "done" });
            return;
          }

          let data;
          try {
            data = JSON.parse(message);
          } catch (err) {
            console.error(err);
            return;
          }

          const text = data.message.content.parts[0];
          if (text) {
            onEvent({
              conversationId: data.conversation_id,
              type: "answer",
              data: {
                text,
                messageId: data.message.id,
                conversationId: data.conversation_id,
              },
            });
          }
        });
      } else {
        return resp.json();
      }
    })
    .then((r) => {
      if (r) {
        onEvent({
          type: "error",
          text: r.detail,
        });
      }
    });
};

const sendMessageFeedback = (accessToken, data) => {
  const methodOptions = {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${accessToken}`,
    },
    body: JSON.stringify(data),
  };

  fetch(
    "https://chat.openai.com/backend-api/conversation/message_feedback",
    methodOptions
  );
};

const removeDialogs = (accessToken) => {
  fetch("https://chat.openai.com/backend-api/conversations", {
    method: "PATCH",
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${accessToken}`,
    },
    body: JSON.stringify({
      is_visible: false,
    }),
  });
};

const genTitle = (accessToken, id, mId) => {
  fetch(`https://chat.openai.com/backend-api/conversation/gen_title/${id}`, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${accessToken}`,
    },
    body: JSON.stringify({
      message_id: mId,
    }),
  });
};

export { chatGptProvider, sendMessageFeedback, removeDialogs, genTitle };
